.sidenav2 {
    width: 300px;
    height: 100%;
    position: fixed;
    z-index: 1;
    top: 63px;
    background: #ee6e73;
    overflow-x: hidden;
    padding: 8px 0;
  }
  
  .sidenav2 a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 20px;
    color: #fff;
    display: block;
  }
  
  .sidenav2 a:hover {
    color: #02181d;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav2 {padding-top: 15px;}
    .sidenav2 a {font-size: 18px;}
  }
